.sidebar-wrapper {
  position: fixed;
  top: 20rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 12rem;
  height: calc(100vh - 20rem);
  background-color: white;
  box-shadow: 0 10px 6px 2px rgba(0, 0, 0, 0.2);
  z-index: 2;
}
